* {
    font-family: inter;
  }
  
  a {
    cursor: pointer;
    border-bottom: 2px solid #0000;
    transition: border-bottom .1s;
  }
  
  a:hover {
    border-bottom: 2px solid;
  }
  
  body {
    font-size: 1rem;
  }
  
  .fullscreen {
    width: 100vw;
    height: 100vh;
  }
  
  #landing-firstScreen {
    color: #fff;
    background: #00529d;
    padding: 1.5rem;
  }
  
  #landing-firstScreen .bg {
    opacity: .5;
    background: url("images/bgfront.png") center / cover no-repeat;
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
  }
  
  #landing-firstScreen .container {
    z-index: 1;
    position: relative;
  }
  
  #header {
    justify-content: space-between;
    align-items: center;
    height: 4rem;
    padding-left: 2rem;
    padding-right: 2rem;
    display: flex;
    position: relative;
  }
  
  #header div {
    display: flex;
  }
  
  #header .left {
    align-items: center;
    gap: 3rem;
  }
  
  #header .left #locationChooser {
    cursor: pointer;
    border-bottom: 2px dashed;
    height: min-content;
  }
  
  #header .center {
    gap: 2.5rem;
  }
  
  #header .right {
    gap: 2rem;
  }
  
  .DelExLogo {
    width: 214px;
    height: 64px;
  }
  
  .landingBegin {
    text-align: center;
    flex-direction: column;
    align-self: center;
    height: min-content;
    margin-top: 14rem;
    margin-bottom: 8rem;
    display: flex;
  }
  
  .landing-header {
    margin: 0 0 2rem;
    font-size: 40px;
    font-weight: 300;
  }
  
  .landing-subheader {
    color: var(--FFFFFF, #fff);
    letter-spacing: 4.2px;
    text-transform: uppercase;
    margin-bottom: 4em;
    font-size: 14px;
    font-style: normal;
    font-weight: 600;
  }
  
  #calculator {
    align-self: center;
    gap: .5rem;
    display: flex;
  }
  
  .calc-textinput {
    color: #444;
    gap: .125rem;
    display: flex;
  }
  
  .calc-textinput div {
    background-color: #fff;
    width: 22rem;
    height: 4rem;
  }
  
  .calc-textinput .left {
    border-radius: .5rem 0 0 .5rem;
  }
  
  .calc-textinput .right {
    border-radius: 0 .5rem .5rem 0;
    width: 16rem;
  }
  
  .calc-button {
    color: #fff;
    cursor: pointer;
    background: #ffa41b;
    border-radius: .5rem;
    width: 15rem;
    height: 4rem;
    transition: background-color .1s;
  }
  
  .calc-button:hover {
    background: #ffb84e;
  }
  
  .buttons-landing {
    text-align: center;
    justify-content: center;
    gap: 2.5rem;
    height: min-content;
    display: flex;
  }
  
  .number-list {
    flex-wrap: wrap;
    gap: 3rem;
    display: flex;
  }
  
  .number-list .number-info {
    width: 14rem;
  }
  
  .number-list .number-info h1, .number-list .number-info h2 {
    color: #0074df;
    margin: 0;
    line-height: 100%;
  }
  
  .number-list .number-info h1 {
    font-size: 4.5rem;
    font-weight: 300;
  }
  
  .number-list .number-info h2 {
    text-transform: uppercase;
    margin-bottom: 1rem;
    font-size: .875rem;
  }
  
  .number-list .number-info p {
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: 150%;
  }
  
  .twopagelayout {
    color: #222;
    background: #f8f8f8;
    gap: 12rem;
    padding: 170px 240px;
    line-height: 150%;
    display: flex;
  }
  
  .twopagelayout div {
    width: 50%;
  }
  
  .twopagelayout .info {
    flex-direction: column;
    gap: 2.5rem;
    display: flex;
  }
  
  .twopagelayout .info h1, .twopagelayout .info h2 {
    margin: 0;
  }
  
  .twopagelayout .info h1 {
    font-size: 36px;
    font-style: normal;
    font-weight: 400;
    line-height: 150%;
  }
  
  .twopagelayout .info h2 {
    font-size: 1.5rem;
    font-weight: 600;
    line-height: 150%;
  }
  
  .twopagelayout .info p {
    line-height: 150%;
  }
  
  .twopagelayout .info .lsp-info-button {
    color: #fff;
    cursor: pointer;
    background-color: #0074df;
    border-radius: .25rem;
    justify-content: center;
    align-items: center;
    padding: 1.5rem 4rem;
    font-size: 1rem;
    font-style: normal;
    font-weight: 600;
    line-height: 100%;
    display: flex;
  }
  
  .twopagelayout .info .contact-list {
    align-items: stretch;
    gap: 2.5rem;
    width: 100%;
    display: flex;
  }
  
  .twopagelayout .info .contact-list .contact {
    width: -moz-fit-content;
    width: fit-content;
  }
  
  .twopagelayout .info .contact-list .contact h6 {
    text-transform: uppercase;
    color: #888;
    font-size: 12px;
    font-style: normal;
    font-weight: 500;
    line-height: 150%;
    margin: 0;
  }
  
  .twopagelayout .info .contact-list .contact p {
    font-size: 16px;
    font-style: normal;
    font-weight: 500;
    line-height: 150%;
  }
  
  .twopagelayout .info .contact-list .contact-separator {
    background-color: #ccc;
    width: 1px;
  }
  
  .cardpagelayout {
    flex-direction: column;
    align-items: center;
    /*width: 100%;*/
    padding: 150px 240px;
    display: flex;
  }
  
  .cardpagelayout .mainText {
    text-align: center;
    flex-direction: column;
    align-items: center;
    margin-bottom: 4rem;
    display: flex;
  }
  
  .cardpagelayout .mainText h1 {
    color: #222;
    text-align: center;
    margin: 0 0 20px;
    font-family: Inter;
    font-size: 36px;
    font-style: normal;
    font-weight: 400;
    line-height: 150%;
  }
  
  .cardpagelayout .mainText h2 {
    color: #888;
    text-align: center;
    width: 34rem;
    font-family: Inter;
    font-size: 18px;
    font-style: normal;
    font-weight: 400;
    line-height: 150%;
  }
  
  .ltp-container {
    gap: 30px;
    display: flex;
  }
  
  .ltp-container .ltp-buisness-card {
    background: #f8f8f8;
    border-radius: 20px;
    width: 585px;
    padding: 50px;
  }
  
  .ltp-container .ltp-buisness-card.ltp-buisness-card-fit {
    width: 14rem;
    padding: 1.75rem;
  }
  
  .ltp-container .ltp-buisness-card .ltp-buisness-header {
    gap: 30px;
    margin-bottom: 55px;
    display: flex;
  }
  
  .ltp-container .ltp-buisness-card .ltp-buisness-header .ltp-buisness-img {
    width: 110px;
    height: 110px;
  }
  
  .ltp-container .ltp-buisness-card .ltp-buisness-header h1 {
    color: #0074df;
    margin: 0;
    font-family: Inter;
    font-size: 24px;
    font-style: normal;
    font-weight: 400;
    line-height: 150%;
  }
  
  .ltp-container .ltp-buisness-card .ltp-buisness-header h2 {
    color: #222;
    font-family: Inter;
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: 150%;
  }
  
  .ltp-container .ltp-buisness-card .ltp-buisness-cost {
    color: #0074df;
    text-align: center;
    flex-direction: column;
    gap: 1rem;
    margin: 1.5rem 1.5rem 4rem;
    font-family: Inter;
    font-size: 20px;
    font-style: normal;
    font-weight: 400;
    line-height: 100%;
    display: flex;
  }
  
  .ltp-container .ltp-buisness-card .ltp-buisness-cost h1, .ltp-container .ltp-buisness-card .ltp-buisness-cost h2 {
    font-weight: inherit;
    font-size: inherit;
    margin: 0;
  }
  
  .ltp-container .ltp-buisness-card .ltp-buisness-cost b {
    color: #0074df;
    font-family: Inter;
    font-size: 44px;
    font-style: normal;
    font-weight: 400;
    line-height: 100%;
  }
  
  .ltp-container .ltp-buisness-card .ltp-buisness-cost h2 {
    color: var(--222222, #222);
    text-align: center;
    text-transform: uppercase;
    font-family: Inter;
    font-size: 14px;
    font-style: normal;
    font-weight: 500;
    line-height: 150%;
  }
  
  .ltp-container .ltp-buisness-card ul {
    flex-direction: column;
    gap: 20px;
    margin-bottom: 3.5rem;
    display: flex;
  }
  
  .ltp-container .ltp-buisness-card ul li {
    align-items: center;
    gap: 1rem;
    display: flex;
  }
  
  .ltp-container .ltp-buisness-card ul li:before {
    content: "";
    background-image: url("images/check.svg");
    background-repeat: no-repeat;
    width: 20px;
    height: 20px;
    display: inline-block;
    position: static;
  }
  
  .ltp-container .ltp-buisness-card .ltp-card-button {
    color: #0074df;
    cursor: pointer;
    border: 1px solid #0074df;
    border-radius: .25rem;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 4rem;
    font-weight: 500;
    display: flex;
  }
  
  .ltp-container .ltp-buisness-card .ltp-card-button.ltp-card-button-small {
    width: -moz-fit-content;
    width: fit-content;
    padding: .75rem 1rem;
    height: 1.5rem;
  }
  
  .faq {
    flex-direction: column;
    gap: 1rem;
    display: flex;
  }
  
  .faq details {
    color: #888;
    background-color: #fff;
    border-radius: .5rem;
    padding: 1.25rem 1.25rem 1.25rem 1.5rem;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: 150%;
  }
  
  .faq details summary {
    color: #222;
    justify-content: space-between;
    gap: 6rem;
    font-size: 16px;
    line-height: 140%;
    display: flex;
  }
  
  .faq details summary:after {
    content: "+";
    border-radius: 1rem;
    flex-shrink: 0;
    justify-content: center;
    align-items: center;
    width: 2rem;
    height: 2rem;
    display: flex;
  }
  
  .faq details[open] summary {
    margin-bottom: 1rem;
  }
  
  .faq details[open] summary:after {
    content: "-";
    color: #fff;
    background-color: #0074df;
  }
  
  #landing-fourthPage {
    padding-top: 8rem;
    padding-bottom: 8rem;
  }
  

.twopagelayout.tpl-about {
  padding-top: 0;
  padding-bottom: 0;
  gap: 2rem;
}


.about-cointainer  .number-info > h1 {
  font-size: 50px; 
  font-weight: 300;
}

.tpl-h1 {
  font-size: 36px;
  font-style: normal;
  font-weight: 400;
  line-height: 150%;
  margin: 0;
}

.quickfix-invis {
  display: none;
}

.quickfix-vis {
  display: block;
}

@media screen and (max-width: 1366px) {
    .twopagelayout {
        padding-left: 8rem !important;
        padding-right: 8rem !important;
        gap: 4rem;
    }
    .ltp-container>.ltp-buisness-card {
        width: 24rem;
    }
}
@media screen and (max-width: 768px) {
  .twopagelayout {
    flex-direction: column;
    text-align: center;
    padding-top: 8rem;
  }
  .twopagelayout div {
    width: auto;
  }
  .number-list {
    justify-content: center;
  }
  .twopagelayout .info .contact-list {
    flex-direction: column;
    align-items: center;
  }
  .twopagelayout .info .contact-list .contact-separator {
    display: none;
  }
  .twopagelayout > .info {
    gap: 2rem;
  }
  .quickfix-invis {
    display: block;
  }
  .quickfix-vis {
    display: none;
  }
  #landing-fourthPage .info {
    order: 3;
  }
  .faq {
    text-align: left;
  }
}

@media screen and (max-width: 512px) {
  .twopagelayout {
    padding-left: 1rem !important;
    padding-right: 1rem !important;
  }
}